<template>
    <div class="router-content">
     	<content-head-top class="router-content-header"></content-head-top>
        <el-row class="router-content-body">
			<el-row style="display:flex; padding:10px;">
				<el-input size="medium" style="width: 300px;font-size:12px;" placeholder="请输入公司名称/编号/负责人/手机号" v-model="input5" class="input-with-select">    
					<el-button slot="append" icon="el-icon-search" @click.native="searchCompany"></el-button>
				</el-input>

				<el-select
					v-model="comp_types.value"
					size="medium"
					style="margin-left: 20px; width:150px;"
					placeholder="公司类型">
					<el-option
						v-for="item in comp_types.options"
						:key="item.value"
						:label="item.label"
						:value="item.value">
					</el-option>
				</el-select>

				<el-select
					v-model="comp_regions.value"
					size="medium"
					style="margin-left: 20px; width:150px;"
					placeholder="归属地区">
					<el-option
						v-for="item in comp_regions.options"
						:key="item.value"
						:label="item.label"
						:value="item.value">
					</el-option>
				</el-select>

				<el-select 
				    v-model="comp_status.value"
					style="margin-left: 20px; width:150px;"
					size="medium"
					placeholder="账号状态">
					<el-option
					v-for="item in comp_status.options"
					:key="item.value"
					:label="item.label"
					:value="item.value"
					:disabled="item.disabled">
					</el-option>
				</el-select>
			</el-row>
			<el-row style="margin: 10px;">
				<el-breadcrumb style="float:left;padding-top:12px;padding-left: 10px;">公司列表</el-breadcrumb>
				<el-button type="success" style="float:right;" @click.native="addCompany">新增</el-button>
			</el-row>
				
			<div style="margin: 10px;">
				<el-table
					:data="companyList"
					align="left"
					border
					max-height="400">
					<el-table-column
					fixed
					prop="cid"
					label="公司编号"
					width="150">
					</el-table-column>
					<el-table-column
					prop="comp_name"
					label="公司名称"
					width="300">
					</el-table-column>
					<el-table-column
					prop="type"
					label="公司类型"
					width="120">
					</el-table-column>
					<el-table-column
					prop="region"
					label="所属地区"
					width="180">
					</el-table-column>
					<el-table-column
					prop="contact_name"
					label="负责人"
					width="120">
					</el-table-column>
					<el-table-column
					prop="contact_phone"
					label="联系电话"
					width="120">
					</el-table-column>
					<el-table-column
					prop="comp_status"
					label="账号状态"
					width="120">
					</el-table-column>
					<el-table-column
					prop="remark"
					label="备注"
					width="150">
					</el-table-column>
					<el-table-column
					fixed="right"
					label="操作"
					width="180">
					<template slot-scope="scope">
						<el-button @click="viewRow(scope.$index, scope.row)" type="text" size="small">查看</el-button>
						<el-button @click="disableRow(scope.$index, scope.row)" type="text" size="small">禁用</el-button>
						<el-button @click.native.prevent="deleteRow(scope.$index, scope.row)" type="text" size="small">删除</el-button>
					</template>
					</el-table-column>
				</el-table>
			</div>	
  			
  		</el-row>
		  <el-dialog
			title="禁用"
			:visible.sync="compDisable.dialogVisible"
			width="30%"
			center>
			<span>确定禁用该公司账号吗？</span>
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" size="mini" @click="disableCompanyAccount">确 定</el-button>
			</span>
		  </el-dialog>
		  <el-dialog
			title="删除"
			:visible.sync="compDelete.dialogVisible"
			width="30%"
			center>
			<span style="text-align">确定删除该公司账号吗？</span>
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" size="mini" @click="deleteCompanyAccount">确 定</el-button>
			</span>
		  </el-dialog>
    </div>
</template>

<script>
	import ContentHeadTop from '../components/ContentHeadTop'
	// import addCompany from '../components/addCompany'
    export default {
    	components: {
    		ContentHeadTop,
		},
		created(){
			this.searchCompany();
			this.searchRegion();
		},
		methods: {
			onValueChange(){
				alert("xxxx");
			},
			viewRow(index, row) {
				console.log(row);
				sessionStorage.setItem('company_detail',JSON.stringify(row));
				this.$router.push('addCompany');
			},
			disableRow(index, row) {
				this.compDisable.dialogVisible = true;
				this.compDisable.rowInfo = row;
				console.log('index=' + index);

			},
			disableCompanyAccount() {
				this.compDisable.dialogVisible = false;
				var rowInfo = this.compDisable.rowInfo;
				console.log(rowInfo['cid']);

				this.$http({
                    method: 'POST',
                    url: '/hugongljb/disable_company_account',
                    data: {
						cid: rowInfo['cid'],
						comp_status: '已停用',
                    }
                    
                }).then((response) => {
					  console.log(response);
					  console.log(response.status);
					  
				});

			},

			deleteRow(index, row) {
				console.log(row);				
				this.compDelete.dialogVisible = true;
				this.compDelete.index = index;
				this.compDelete.rowInfo = row;
			},
			deleteCompanyAccount() {
				this.compDelete.dialogVisible = false;						
				var rowInfo = this.compDelete.rowInfo;
				console.log(rowInfo['cid']);

				this.$http({
                    method: 'POST',
                    url: '/hugongljb/delete_company_account',
                    data: {
                        cid: rowInfo['cid'],
                    }
                    
                }).then((response) => {
					  console.log(response);
					  console.log(response.status);
					  if(response.status == 200) {
						  this.companyList.splice(this.compDelete.index, 1);
					  }
				});
			},
			
			addCompany() {
				sessionStorage.setItem('company_detail', null);
				this.$router.push('addCompany');
			},
			cancel() {
				this.goBack();
			},
			async searchCompany() {
				this.$http.get("/hugongljb/get_companys")
				.then((response) => {
					  console.log(response);
					  this.companyList = response.data.data.list;
					console.log(this.companyList);
					// for(let item of response.data.data.list) {
					// 	this.companyList;
					// 	console.log(item.name);
					// 	console.log(item.cid);
					// 	console.log(item.type);
					// 	console.log(item.region);
					// }
				})
			},
			async searchRegion() {
				this.$http.get("/hugongljb/get_company_regions")
				.then((response) => {
					console.log("response:");
					console.log(response);
					var list = response.data.data.list;
					for(var i = 0; i < list.length; i++) {
						var item = {value:i, label:list[i].region};									
						this.comp_regions.options[i] = item;
					}
				})
			},
			remoteMethod() {
				alert("remoteMethod");
			}
    },
    data() {
      return {
		  companyList:[],
		  regionList:[],
    
        tableData4: [{
          companyId: '25055219',
          companyName: '诚愿医疗护理有限公司',
          companyType: '上海',
          region: '上海普陀区',
          principal: '王小虎',
          contactNumber: 200333,
		  accountStatus: '已启用',
		  remarks:'xxx'
        }, {
          companyId: '25055219',
          companyName: '诚愿医疗护理有限公司',
          companyType: '上海',
          region: '上海普陀区',
          principal: '王小虎',
          contactNumber: 200333,
		  accountStatus: '已启用',
		  remarks:'xxx'
        }, {
          companyId: '25055219',
          companyName: '诚愿医疗护理有限公司',
          companyType: '上海',
          region: '上海普陀区',
          principal: '王小虎',
          contactNumber: 200333,
		  accountStatus: '已启用',
		  remarks:'xxx'
        }, {
          companyId: '25055219',
          companyName: '诚愿医疗护理有限公司',
          companyType: '上海',
          region: '上海普陀区',
          principal: '王小虎',
          contactNumber: 200333,
		  accountStatus: '已启用',
		  remarks:'xxx'
        }, {
          companyId: '25055219',
          companyName: '诚愿医疗护理有限公司',
          companyType: '上海',
          region: '上海普陀区',
          principal: '王小虎',
          contactNumber: 200333,
		  accountStatus: '已启用',
		  remarks:'xxx'
        }, {
          companyId: '25055219',
          companyName: '诚愿医疗护理有限公司',
          companyType: '上海',
          region: '上海普陀区',
          principal: '王小虎',
          contactNumber: 200333,
		  accountStatus: '已启用',
		  remarks:'xxx'
        }, {
          companyId: '25055219',
          companyName: '诚愿医疗护理有限公司',
          companyType: '上海',
          region: '上海普陀区',
          principal: '王小虎',
          contactNumber: 200333,
		  accountStatus: '已启用',
		  remarks:'xxx'
		}],
		comp_types:{
			options: [{
			value: '选项1',
			label: '1'
			}, {
			value: '选项2',
			label: '2',
			// disabled: true
			}],
			value: ''
		},
		comp_status:{
			options: [{
			value: '选项1',
			label: '已启用'
			}, {
			value: '选项2',
			label: '已停用',
			// disabled: true
			}],
			value: ''
		},
		comp_regions:{
			options:[],
			value:''
		},
		compDisable:{
			rowInfo:'',
			dialogVisible:false,
		},
		compDelete:{
			rowInfo:'',
			index:'',
			dialogVisible:false,
		},
      }
    }
    }
</script>


<style lang="less">
	@import '../style/mixin';


	.el-table th{
        text-align:center;/** 设置水平方向居中 */
        vertical-align:middle/** 设置垂直方向居中 */
    }
	td{
		text-align:center;
	}
	.el-dialog--center .el-dialog__body {
    	text-align: center;
	}

</style>



